			[],
			[]
		]);
	}

	

	function startGame(id) {
		$$invalidate(0, gameId = id || parseInt(Math.random() * 1000000, 10));
		console.log("Start new game with id ", gameId);
		let deck = new Game.Deck(gameId);
		let cards = deck.toTableau();
		set_store_value(history, $history = []);
		set_store_value(columns, $columns = cards.concat([[], [], [], [], [], [], [], []]));
		set_store_value(moves, $moves = 0);

		if (timer) {
			resetTimer();
		}

		timer = startTimer();
		set_store_value(started, $started = true);
	}

	

	function startTimer() {
		return setInterval(
			function () {
				set_store_value(timeElapsed, $timeElapsed += 1);
			},
			1000
		);
	}

	

	function resetTimer() {
		clearInterval(timer);
		set_store_value(timeElapsed, $timeElapsed = 0);
	}

	
	
	
	let gameId;

	function handleAction(event) {
		console.log("[action]", event.detail);
		let command = event.detail.command;

		switch (command) {
			case "undo":
				Game.undo();
				break;
			case "settings":
				console.log("change settings");
				break;
			case "newgame":
				$$invalidate(5, showModal = true);
				break;
			case "startgame":
				let id = event.detail.gameId;
				startGame(id);
				$$invalidate(5, showModal = false);
				break;
			case "restart":
				console.log("Restart same game");
				if (confirm("Are you sure you wish to restart this game?")) {
					startGame(gameId);
					break;
				}
			case "hint":
				console.log("Hint for next move");
				break;
			case "cancelmodal":
				$$invalidate(5, showModal = false);
				break;
			default:
		}
	}

	

	/* init settings for new game */
	let timer;

	let showModal;
	$: $$invalidate(5, showModal = true);

	return [
		gameId,
		$columns,
		$moves,
		$history,
		$timeElapsed,
		showModal,
		Game,
		handleAction
	];
}

